import binarySearch from '../../helpers/binary_search';

function validateSelectedSpaceConversion({ data, inSelectedSpace, firstIndex, lastIndex, ignoreDiscontinuities }) { // eslint-disable-line no-unused-vars
    let correct = data.slice(firstIndex + 1, lastIndex);
    if (ignoreDiscontinuities) {
        correct = correct.filter(([_x, y]) => (y !== null && y !== undefined));
    }

    if (correct.length !== inSelectedSpace.length) {
        console.log({ // eslint-disable-line no-console
            data,
            attempt: inSelectedSpace.map(([x, y]) => [undateify(x), y]),
            correct: correct.map(([x, y]) => [undateify(x), y]),
            sdl: window.sdl
        });
        window.tacomaDataPaused = true;
        throw new Error('Failed to select via swap');
    }

    for (let i = 0; i < correct.length; i++) {
        if (undateify(correct[i][0]) !== undateify(inSelectedSpace[i][0]) || correct[i][1] !== inSelectedSpace[i][1]) {
            console.log({ // eslint-disable-line no-console
                i,
                attempt: inSelectedSpace.map(([x, y]) => [undateify(x), y]),
                correct: correct.map(([x, y]) => [undateify(x), y]),
                correctX: undateify(correct[i][0]),
                correctY: correct[i][1],
                attemptX: undateify(inSelectedSpace[i][0]),
                attemptY: inSelectedSpace[i][1]
            });
            window.tacomaDataPaused = true;
            throw new Error('Failed to select via swap');
        }
    }
}

/**
 * Converts from data space to selected space
 *
 * @param {Array<Array<Number>>} data
 * @param {Array} [swap]
 * @param {Number} minX
 * @param {Number} maxX
 * @param {Boolean} ignoreDiscontinuities
 * @param {Boolean} square
 * @return {Array<Array<Number>>}
 */
export default function dataSpaceToSelectedSpace({ data, swap, minX, maxX, ignoreDiscontinuities, square }) {
    if (data.length) {
        if (minX > data[data.length - 1][0] || maxX < data[0][0]) {
            return {
                data: [
                    [minX, null],
                    [maxX, null]
                ],
                firstInterpolated: false,
                lastInterpolated: false,
                addAtFirstIndex: false,
                addAtLastIndex: false
            };
        }
    }

    let firstIndex = binarySearch(data, minX, { searchType: 'before', returnIndex: true }) || 0;
    let lastIndex = binarySearch(data, maxX, { searchType: 'after', returnIndex: true }) || 0;

    let inSelectedSpace;
    let ignoreAtStart = 0;

    if (swap && swap.data) {
        inSelectedSpace = swap.data;

        if (swap.firstIndex === firstIndex && swap.minX === minX && swap.maxX <= maxX && swap.data.length) {
            if (swap.firstInterpolated) {
                ignoreAtStart++;
            }

            if (swap.lastInterpolated) {
                inSelectedSpace.pop();
            }

            if (swap.addAtFirstIndex) {
                ignoreAtStart++;
            }

            if (swap.addAtLastIndex) {
                inSelectedSpace.pop();
            }

            const swapDataLength = swap.data.length - ignoreAtStart;

            inSelectedSpace.pop();
            if (ignoreAtStart > inSelectedSpace.length) {
                ignoreAtStart = inSelectedSpace.length;
            }

            const lastX = inSelectedSpace.length ? inSelectedSpace[inSelectedSpace.length - 1][0] : null;

            for (let i = Math.max(swapDataLength - 1, 0); i < lastIndex - firstIndex - 1; i++) {
                const point = data[i + firstIndex + 1];

                if (ignoreDiscontinuities && (point[1] === null || point[1] === undefined)) {
                    continue;
                }

                if (lastX !== null && point[0] <= lastX) {
                    continue;
                }

                inSelectedSpace.push(point);
            }

            // validateSelectedSpaceConversion({
            //     data,
            //     inSelectedSpace: inSelectedSpace.slice(ignoreAtStart),
            //     firstIndex, lastIndex, ignoreDiscontinuities
            // });
        } else {
            for (let i = 0; i < lastIndex - firstIndex - 1; i++) {
                inSelectedSpace[i] = data[i + firstIndex + 1];
            }

            inSelectedSpace.splice(lastIndex - firstIndex - 1);

            if (ignoreDiscontinuities) {
                inSelectedSpace = inSelectedSpace.filter(([_x, y]) => (y !== null && y !== undefined));
            }
        }
    } else {
        inSelectedSpace = data.slice(firstIndex + 1, lastIndex);

        if (ignoreDiscontinuities) {
            inSelectedSpace = inSelectedSpace.filter(([_x, y]) => (y !== null && y !== undefined));
        }
    }

    const addAtFirstIndex = firstIndex !== -1 && minX < data[firstIndex][0];
    if (addAtFirstIndex) {
        if (ignoreAtStart) {
            inSelectedSpace[ignoreAtStart - 1] = data[firstIndex];
            ignoreAtStart--;
        } else {
            inSelectedSpace.unshift(data[firstIndex]);
        }
    }

    const addAtLastIndex = lastIndex !== -1 && maxX > data[lastIndex][0];
    if (addAtLastIndex) {
        inSelectedSpace.push(data[lastIndex]);

        if (square) {
            inSelectedSpace.push([maxX, data[lastIndex][1]]);
        }
    }

    const firstInterpolated = (inSelectedSpace.length <= ignoreAtStart) || undateify(inSelectedSpace[ignoreAtStart][0]) !== undateify(minX);
    if (firstInterpolated) {
        const point = [minX, interpolateFirst(data, firstIndex, minX)];

        if (ignoreAtStart) {
            inSelectedSpace[ignoreAtStart - 1] = point;
            ignoreAtStart--;
        } else {
            inSelectedSpace.unshift(point);
        }
    }

    const lastInterpolated = !square && (inSelectedSpace.length < 2 || undateify(inSelectedSpace[inSelectedSpace.length - 1][0]) !== undateify(maxX));
    if (lastInterpolated) {
        inSelectedSpace.push([maxX, interpolateLast(data, lastIndex - 1, maxX)]);
    }

    for (let i = 0; i < ignoreAtStart; i++) {
        inSelectedSpace.shift();
    }

    return {
        data: inSelectedSpace,
        minX,
        maxX,
        firstIndex,
        firstInterpolated,
        lastInterpolated,
        addAtFirstIndex,
        addAtLastIndex
    };
}

function undateify(potentialDate) {
    if (potentialDate instanceof Date) {
        return potentialDate.valueOf();
    }

    return potentialDate;
}

/**
 * Finds the point at the boundary via interpolation
 *
 * @param {Array<Array<Number>>} data
 * @param {Number} firstIndex
 * @param {Number} minX
 * @return {null|*}
 */
function interpolateFirst(data, firstIndex, minX) {
    if (firstIndex < 0) {
        return null;
    }

    if (firstIndex + 1 >= data.length) {
        return null;
    }

    const [xBefore, yBefore] = data[firstIndex];
    const [xAfter, yAfter] = data[firstIndex + 1];

    if (minX === xBefore && yBefore !== null) {
        return yBefore;
    }

    if (minX === xAfter && yAfter !== null) {
        return yAfter;
    }

    if (yBefore === null || yAfter === null) {
        return null;
    }

    const percent = (minX - xBefore)/(xAfter - xBefore);
    if (percent < 0) {
        return null;
    }

    return percent*(yAfter - yBefore) + yBefore;
}

/**
 * Finds the point at the boundary via interpolation
 *
 * @param {Array<Array<Number>>} data
 * @param {Number} lastIndex
 * @param {Number} maxX
 * @return {null|*}
 */
function interpolateLast(data, lastIndex, maxX) {
    if (lastIndex >= data.length - 1) {
        return null;
    }

    if (lastIndex < 0) {
        return null;
    }

    const [xBefore, yBefore] = data[lastIndex];
    const [xAfter, yAfter] = data[lastIndex + 1];

    if (maxX === xBefore && yBefore !== null) {
        return yBefore;
    }

    if (maxX === xAfter && yAfter !== null) {
        return yAfter;
    }

    if (yBefore === null || yAfter === null) {
        return null;
    }

    const percent = (maxX - xBefore)/(xAfter - xBefore);
    if (percent > 1) {
        return null;
    }

    return percent*(yAfter - yBefore) + yBefore;
}
